import React from "react";

interface ProductCardProps {
  image: string;
  title: string;
  price: string;
  oldPrice?: string;
}

const ProductCard: React.FC<ProductCardProps> = ({ image, title, price, oldPrice }) => {
  return (
    <div className="flex flex-col items-center w-full">
      <img
        src={image}
        alt={title}
        className="w-full aspect-[3/4] object-contain mt-6 mb-6"
      />
      <div className="w-full text-center px-2 pb-6">
        <div
          className="text-[18px] text-gray-500 font-normal mb-2 leading-tight"
          style={{ fontFamily: "Menlo, monospace" }}
        >
          {title}
        </div>
        <div className="flex justify-center items-baseline gap-2">
          <span
            className="text-[15px] text-gray-500 font-normal"
            style={{ fontFamily: "Menlo, monospace" }}
          >
            {price}
          </span>
          {oldPrice && (
            <span
              className="text-[13px] line-through text-gray-500"
              style={{ fontFamily: "Menlo, monospace" }}
            >
              {oldPrice}
            </span>
          )}
        </div>
      </div>
    </div>
  );
};

export default ProductCard;
